

// Type pour un article

export interface Article {
  _id: string;
  title: string;
  content: string;
  author: string;
}

//  base de données en mémoire
let articles: Article[] = [
  {
    _id: '1',
    title: 'Premier article',
    content: 'Ceci est le contenu du premier article.',
    author: 'ALI', 
    
  },
  {
    _id: '2',
    title: 'Deuxième article',
    content: 'Ceci est le contenu du deuxième article.',
    author: 'AZA',
    
  },
];

// Récupérer tous les articles
export async function getArticles(): Promise<Article[]> {
  try {
    const res = await fetch('http://localhost:3000/articles');
    if (!res.ok) {
      throw new Error('Échec de la récupération des articles');
    }
    const data = await res.json();
    return data;
  } catch (error) {
    console.error('Erreur lors de la récupération des articles:', error);
    return [];
  }
}


// Récupérer un article par son ID
export async function getArticleById(id: string): Promise<Article | null> {
  // Simuler un délai de chargement
  await new Promise((resolve) => setTimeout(resolve, 500));
  const article = articles.find((article) => article._id === id);
  return article || null;
}

// Créer un nouvel article
export async function createArticle(article: Omit<Article, '_id'>): Promise<string> {
  // Simuler un délai de chargement
  await new Promise((resolve) => setTimeout(resolve, 500));
  const newArticle = {
    _id: (articles.length + 1).toString(), // Simuler un ID unique
    ...article,
    createdAt: new Date(),
  };
  articles.push(newArticle);
  return newArticle._id;
}

// Modifier un article
export async function updateArticle(id: string, article: Partial<Article>): Promise<void> {
  // Simuler un délai de chargement
  await new Promise((resolve) => setTimeout(resolve, 500));
  const index = articles.findIndex((a) => a._id === id);
  if (index !== -1) {
    articles[index] = { ...articles[index], ...article };
  }
}

// Supprimer un article
export async function deleteArticle(id: string): Promise<void> {
  // Simuler un délai de chargement
  await new Promise((resolve) => setTimeout(resolve, 500));
  articles = articles.filter((article) => article._id !== id);
}